body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.spinner {
  position: relative;
  top:250px;
  left:50%;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 9px solid;
  border-color: #dbdcef;
  border-right-color: #47ff5d;
  animation: spinner-d3wgkg 1s infinite linear;
}

@keyframes spinner-d3wgkg {
  to {
     transform: rotate(1turn);
  }
}
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;

}
.navbar{
  padding: 10px;
  text-align: center;
  font-size: 16px;
  box-shadow: 0 0 10px #333;
  position: fixed;
  width:100%;
  height: 65px;
  background-color: white;
}
.Blogs{
  width:100%;
  height:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.postSection{
  position: relative;
  top:50px;
  z-index: -1;
  width:40%;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 20px;
  
}
.title{
  font-size: 18px;
  font-weight: 750;
  padding: 5px 0;
}
.autherName{
  font-style: italic;
}
.category{
  font-weight: 600;
  text-decoration-line: underline;
  text-decoration-style: dotted;
}
.content{
  margin-top: 10px;
  margin-bottom: 10px;
}
.tags{
  word-spacing: 5px;
  color:rgb(0, 85, 255);
  text-decoration: underline;
}
.pagination{
  width:100%;
  background-color: white;
  position: fixed;
  bottom: 0;
  box-shadow: 10px 0 3px #333;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding:15px;
}
.pageSection{
  font-weight: 700;
}
button{
    border: 1px solid #928f8f;
    padding: 5px;
    border-radius: 3px;
    font-weight: 600;
    margin-right: 3px;
    cursor: pointer;
}
.blogs{
  margin-bottom: 120px;
  
}

